<template>
  <div>
    <div class="border border-dark pl-4 pr-4">
      <!-- 補助金詳細 -->
      <b-row>
        <div class="title-fs-2 mt-2">{{ $t("SUBSIDYDETAIL") }}</div>
        <br /><br />
      </b-row>
      <b-row class="mb-2">
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{
                $t("SUBSIDYGRANTJUDGEMENTRESULT")
              }}</label>
            </b-col>
            <b-col>
              {{ subsidyinfo.judgementResult }}
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{ $t("SITUATION") }}</label>
            </b-col>
            <b-col xl="3">
              <b-form-select
                :plain="true"
                v-model="selectedStaus"
                :options="situationOptions"
                value-field="item"
                text-field="name"
              ></b-form-select>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{
                $t("ASSESSMENTREQUESTDATE")
              }}</label>
            </b-col>
            <b-col>{{ subsidyinfo.requestDate }} </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{
                $t("APPLICATIONDATE2")
              }}</label>
            </b-col>
            <b-col xl="4">
              <b-input-group>
                <b-form-input
                  v-model="applicationDate"
                  type="text"
                  autocomplete="off"
                ></b-form-input>
                <b-input-group-append>
                  <b-form-datepicker
                    @context="onContext"
                    button-only
                    right
                  ></b-form-datepicker>
                </b-input-group-append>
              </b-input-group>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="10">
          <b-row class="d-flex align-items-center">
            <b-col xl="2">
              <label class="highLightHeaderLabel">{{ $t("REMARKS") }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="subsidyinfo.remarks"
                pattern="/^([一-龠]+|[ぁ-ゔ]+|[ァ-ヴー]+|[a-zA-Z0-9_-]+|[ａ-ｚＡ-Ｚ０-９]+[々〆〤]+){1,10}$/u"
                maxlength="100"
                class="form-control"
                value="〇〇〇〇〇〇〇〇〇補助金の査定"
              ></b-form-input>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="10">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="2">
              <label class="highLightHeaderLabel">{{ $t("CONTACT") }}</label>
            </b-col>
            <b-col cols="7" xl="3">
              <b-form-input
                v-model="subsidyinfo.personIncharge"
                class="form-control"
                value="山田太郎"
              ></b-form-input>
            </b-col>
            <b-col cols="2" xl="2">
              <b-button @click="showPersonSelect" variant="primary">{{
                $t("SELECTCONTACT")
              }}</b-button>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
    </div>
    <br />
    <b-button @click="updateSubsidyInfo" variant="primary"
      >{{ $t("SUBSIDYASSESSMENTUPDATE") }}
      <i v-if="isBusy" class="fa fa-spinner fa-spin" />
    </b-button>
    <br /><br />
    <b-modal
      id="person-modal"
      ref="person-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <b-row>
        <b-col>
          <b-button
            @click="cancel"
            class="float-right mt-1"
            variant="primary"
            >{{ $t("CLOSE") }}</b-button
          >
          <label class="font-weight-bold float-left mt-2">{{
            $t("SELECTCONTACT")
          }}</label>
        </b-col>
      </b-row>

      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <b-form-group>
              <b-form-radio-group
                id="radio-group-1"
                v-model="personSelect"
                :options="personOptions"
                class="radioControl mb-3 pt-2"
                size="md"
                stacked
              ></b-form-radio-group>
            </b-form-group>
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button @click="getPerson" variant="primary" class="float-right"
            >{{ $t("ADD") }}
          </b-button>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";

export default {
  name: "SubsidyInfoEdit",
  components: {},
  data() {
    return {
      subsidyinfo: {
        judgementResult: "",
        status: "",
        requestDate: "",
        remarks: "",
        personIncharge: "",
      },
      selectedStaus: "",
      applicationDate: "",
      formatted: "",
      selected: "",
      situationOptions: [
        { item: "01", name: this.$t("SUBSIDY.ASSESSMENTREQUEST") },
        { item: "02", name: this.$t("SUBSIDY.UNDERASSESSMENT") },
        { item: "03", name: this.$t("SUBSIDY.APPLYING") },
        { item: "04", name: this.$t("SUBSIDY.APPLIED") },
      ],
      personSelect: "",
      personOptions: [],
      personId: "",
      selectedPerson: {},
      subsidyType: "",
      isBusy: false,
    };
  },
  computed: {
    ...mapGetters({
      assessment_Details: "getAssesmentNo",
      client_details: "getClientData",
    }),
  },
  mounted() {
    this.getClientData();
    this.getPersonList();
  },
  methods: {
    /**
     * @description function to hide the popup-modal
     * @name cancel
     */
    cancel() {
      this.$refs["person-modal"].hide();
    },
    /**
     * @description function to show the popup-modal
     * @name showPersonSelect
     */
    showPersonSelect() {
      this.personSelect = this.personId;
      this.$refs["person-modal"].show();
    },
    /**
     * @description function to get the selected person in the popup modal
     * @name getPerson
     */
    getPerson() {
      this.selectedPerson = this._.find(this.personOptions, {
        value: this.personSelect,
      });
      this.subsidyinfo.personIncharge = this.selectedPerson.text;
      this.personId = this.selectedPerson.value;
      this.$refs["person-modal"].hide();
    },
    /**
     * @description function to fetch the person details
     * @name getPersonList
     */
    async getPersonList() {
      const { data } = await BasicInfoApis.postAPI(endPoint.MASTERREADMPERSON, {
        fieldlist: ["person_id", "person_name"],
      });
      if (data.success) {
        let responseData = data.data;
        let listData = [];
        if (responseData.length) {
          responseData.forEach((item) => {
            let iteratePerson = {
              text: item.person_name ? item.person_name : "",
              value: item.person_id ? item.person_id : "",
            };
            listData.push(iteratePerson);
          });
          this.personOptions = listData;
        } else {
          this.personOptions = [];
        }
      } else {
        this.personOptions = [];
      }
    },
    /**
     * @description function to fetch the subsidyInfo details
     * @name getClientData
     */
    async getClientData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADCLIENTSUBSIDYASSESSMENTDETAIL,
        {
          client_no: this.client_details.client_no,
          assessment_no: this.assessment_Details.assesmentno,
        }
      );
      if (data.success) {
        let subsidyData = data.data.subsidyAssessentObject;
        let personData = data.data.personObject;
        let ResultData = data.data.subsidyChartRObject;
        //subsidyInfo
        if (subsidyData.assessment_dt != null) {
          let Dt = new Date(subsidyData.assessment_dt);
          let AssesesMentDate =
            Dt.getFullYear() +
            "/" +
            this.formatMonth(parseInt(Dt.getMonth() + 1).toString()) +
            "/" +
            this.formatMonth(Dt.getDate().toString());
          this.subsidyinfo.requestDate = AssesesMentDate;
        }
        if (subsidyData.request_dt != null) {
          let Dt2 = new Date(subsidyData.request_dt);
          let assesmentRequestdate =
            Dt2.getFullYear() +
            "/" +
            this.formatMonth(parseInt(Dt2.getMonth() + 1).toString()) +
            "/" +
            this.formatMonth(Dt2.getDate().toString());
          this.applicationDate = assesmentRequestdate;
        }
        this.subsidyinfo.judgementResult = ResultData.judge_result;
        this.selectedStaus = subsidyData.assessment_sts_kbn;
        this.subsidyinfo.remarks = subsidyData.remarks;
        if (personData != null) {
          this.subsidyinfo.personIncharge = personData.person_name;
          this.personId = personData.person_id;
        }
        this.subsidyType = subsidyData.subsidy_type;
      }
    },
    /**
     * @description function to format the Date (MM)
     * @name formatMonth
     */
    formatMonth(item) {
      let data = "";
      if (item.slice(0, 2).length == 1) {
        data += "0" + item.slice(0, 2);
      } else {
        data += item.slice(0, 2);
      }
      return data;
    },
    /**
     * @description function to update the subsidyinfo
     * @name updateSubsidyInfo
     */
    async updateSubsidyInfo() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.SUBSIDYASSESSMENTADDEDIT,
        {
          assesment_no: this.assessment_Details.assesmentno,
          subsidy_assesment_object: {
            remarks: this.subsidyinfo.remarks,
            client_no: this.client_details.client_no,
            subsidy_type: this.subsidyType,
            assessment_dt: this.subsidyinfo.requestDate,
            request_dt: this.applicationDate,
            assessment_sts_kbn: this.selectedStaus,
            create_id: this.client_details.client_no,
            update_id: this.client_details.client_no,
            del_flg: false,
            assessment_person_id: this.personId,
          },
        }
      );
      this.isBusy = false;
      if (data.success) {
        this.getClientData();
        this.$router.push({ name: "client_mgmt_subsidy_assessment_detail" });
      }
    },
    /**
     * @description function to get the application on changing on the datePicker
     * @name onContext
     */
    onContext(ctx) {
      console.log(ctx);
      if (ctx.selectedDate != null) {
        let Dt = new Date(ctx.selectedDate);
        console.log(Dt);
        this.applicationDate =
          Dt.getFullYear() +
          "/" +
          this.formatMonth(parseInt(Dt.getMonth() + 1).toString()) +
          "/" +
          this.formatMonth(Dt.getDate().toString());
      }
    },
  },
};
</script>

<style>
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
}
</style>